
import { computed, defineComponent } from "vue";

export default defineComponent({
  name: "App",
  template: "#itemToolTip",
  props: {
    x: { type: Number, default: 0 },
    y: { type: Number, default: 0 },
    bar: Object,
  },
  setup(props) {
    const position = computed(() => {
      return {
        top: `${props.y + 10}px`,
        left: `${props.x + 10}px`,
        bar: props.bar,
      };
    });

    return {
      position,
    };
  },
});
